import './card.css'
const Card = (props)=>{
    return (
        <>
        <div className='card'>
         <section>
          <h3>{props.dt}</h3>
            <p>
              {/* <span>{props.three}{'  am '}</span> */}
              <span>{props.six}{' am '}</span>
              {/* <span>{props.nine}{' am '}</span> */}
              <span>{props.twelve}{' pm '}</span>
              {/* <span>{props.fifteen-12}{' pm '}</span> */}
              <span>{'0'}{props.eighteen-12}{' pm '}</span>
              <span>{props.twenty_four}{' am '}</span>
            </p>
         </section>

          <section>
            <p className='temp'>
              <span>{props.six_temp}&#176;c</span>
              <span>{props.twelve}&#176;c</span>
              <span>{props.eighteen_temp}&#176;c</span>
              <span>{props.twenty_four_temp}&#176;c</span>
            </p>
          </section>
          
            <section>
              <p className='icon'>
                <span>{props.icon_6}</span>
                <span>{props.icon_12}</span>
                <span>{props.icon_18}</span>
                <span>{props.icon_24}</span>
              </p>
            </section>
        </div>
        </>
    );
}

export default Card;